import React from 'react';
import {NavLink} from 'react-router-dom';
import style from './Header.module.css';
import {DataType} from "./HeaderContainer";



type LoginType = {
    data: DataType
    isAuth: boolean
    logout: ()=> void

}


export const Header = (data: LoginType) => {




    return (
        <header className={style.header}>
            <img alt={'this is photo'}
                 src='data:image/jpeg;base64,'/>

            <div className={style.loginBlock}>
                {data.isAuth
                    ?<div>{ data.data.login} - <button className={style.LogOut} onClick={data.logout}>Log out</button></div>
                    : <NavLink to={'/login'} className={style.Login}>Login</NavLink>}
            </div>
        </header>
    )
}
